.first-page {
  background: url("../src/Assets/Imgs/bg.png") center / cover no-repeat;
  height: calc(100vh - 100px);
  margin-top: 100px;

  &__content {
    display: flex;
    flex-direction: column;
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #fff;
    gap: 3rem;
  }

  &__title {
    font-size: 40px;
    line-height: 150%;
    white-space: nowrap;
  }

  &__subtitle {
    font-family: Montserrat;
    font-size: 18px;
    line-height: 150%;
  }

  &__button {
    margin: 0 auto;
    border-radius: 40px;

    &:hover {
      background: #432e1f;
      color: #fff;
      transition: 0.6s;

      &::after {
        filter: brightness(0) invert(1);
        transition: 0.6s;
      }
    }
  }
}

@media (max-width: 767px) {
  .first-page__title {
    font-size: 7vw;
  }

  .button {
    width: 100%;
    padding: 15px 30px;
  }
}
